<template>
  <component
    v-if="productDataIsLoaded"
    :product="product"
    :vendor="vendor"
    :is="resolvedComponent"
    v-bind="computedProperties"
  />
</template>
<script>
import { mapActions, mapState } from 'vuex';
import componentResolver from '../../../mixins/component-resolver';

export default {
  mixins: [componentResolver('views', __dirname)],

  data() {
    return {
      productDataIsLoaded: false
    };
  },

  computed: {
    ...mapState({
      product: state => state.product.entity,
      vendor: state => state.product.vendor
    })
  },

  created() {
    this.$log.debug('PdpView<generic>::created()');

    // since we are working with models the product data might be already
    // pre-populated partially, so in that case we should render the PDP
    // immediately and then load the remaining data in the meantime. So if
    // the product model is already (partially) populated, we set the
    // productDataIsLoaded flag to true to make the PDP page render.
    this.productDataIsLoaded = Object.keys(this.product).length > 0;

    if (this.productDataIsLoaded) {
      this.$log.debug('PdpView<generic>::created() -> product data has been pre-populated');
    }

    const productCode = this.$route.params.code;

    Promise.all([
      this.loadProduct(productCode),
      this.loadVendor(productCode),
    ]).then(() => {
      this.productDataIsLoaded = true;
    }).catch((error) => {
      this.$log.error('PdpView<generic>::created() -> could not load required data for PDP', error);

      this.$router.push({
        name: 'error',
        query: { errorCode: 'NOT_FOUND' }
      });
    });
  },

  methods: {
    ...mapActions([
      'loadProduct',
      'loadVendor'
    ])
  }
};
</script>
